import { FocusMonitor } from "@angular/cdk/a11y";
import { BooleanInput, coerceBooleanProperty } from "@angular/cdk/coercion";
import { DOWN_ARROW, ENTER, ESCAPE, SPACE, TAB, UP_ARROW } from "@angular/cdk/keycodes";
import { CdkConnectedOverlay, CdkOverlayOrigin, ConnectedOverlayPositionChange } from "@angular/cdk/overlay";
import { Platform } from "@angular/cdk/platform";
import { CdkVirtualScrollViewport } from "@angular/cdk/scrolling";
import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, ContentChild, ContentChildren, DoCheck, ElementRef, EventEmitter, forwardRef, Host, Inject, Input, NgZone, OnDestroy, OnInit, Optional, Output, QueryList, Self, SimpleChanges, TemplateRef, ViewChild, ViewEncapsulation } from "@angular/core";
import { ControlValueAccessor, FormControl, FormGroupDirective, NgControl, NgForm } from "@angular/forms";
import { BehaviorSubject, combineLatest, fromEvent, Subject } from "rxjs";
import { delay, filter, takeUntil } from "rxjs/operators";
import { reqAnimFrame } from "tds-ui/core/animation";
import { ButtonSize, NgClassType } from "tds-ui/core/config";
import { TDSOptgroup, TDSOption } from "tds-ui/core/option";
import { TDSFormField, TDSFormFieldControl, TDS_FORM_FIELD } from "tds-ui/form-field";
import { TDSIsErrorState } from "tds-ui/shared/common";
import { InputBoolean, TDSHelperArray, TDSHelperObject, TDSHelperString, TDSSafeAny } from "tds-ui/shared/utility";
import { TDSSelectSearchComponent } from "./select-search";
import {
    TDSFooterTemplateDirective,
    TDSHeaderTemplateDirective,
    TDSLabelTemplateDirective,
    TDSLoadingSpinnerTemplateDirective,
    TDSLoadingTextTemplateDirective,
    TDSMultiLabelTemplateDirective,
    TDSNotFoundTemplateDirective,
    TDSOptionTemplateDirective,
    TDSTagTemplateDirective,
    TDSTypeToSearchTemplateDirective
} from "./select.directive";
import { TDSFilterOptionType, TDSSelectItemInterface, TDSSelectModeType } from "./select.type";

export const TDSDefaultFilterOption: TDSFilterOptionType = (searchValue: string, item: TDSSelectItemInterface): boolean => {
    if (item && item.textField) {
        return item.textField.toString().toLowerCase().indexOf(searchValue.toLowerCase()) > -1;
    } else {
        return false;
    }
};
@Component({
    selector: 'tds-select',
    templateUrl: "./select.html",
    providers: [
        {
            provide: TDSFormFieldControl,
            useExisting: forwardRef(() => TDSSelect),

        }
    ],
    host: {
        'class': 'w-full block relative box-border m-0 p-0',
        '[class.cursor-pointer]': "!disabled",
        // '[class.cursor-not-allowed]':"disabled"
    },
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None,

})


export class TDSSelect implements ControlValueAccessor, TDSFormFieldControl<any>, OnDestroy, DoCheck, OnInit, AfterViewInit {
    private _destroy = new Subject<void>();
    stateChanges = new Subject<void>();
    private listOfValue$ = new BehaviorSubject<any[]>([]);
    private listOfTemplateItem$ = new BehaviorSubject<TDSSelectItemInterface[]>([]);
    static nextId = 0;

    focused = false;
    get empty() {
        return !TDSHelperArray.hasListValue(this.listOfValue);
    }
    listOfTopItem: TDSSelectItemInterface[] = [];
    activatedValue: any | null = null;
    listOfValue: Array<any> = [];
    inputValue: string | null = null;
    triggerWidth: number | null = null;
    dropDownPosition: 'top' | 'center' | 'bottom' = 'bottom';
    isReactiveDriven = false;
    private listOfTagAndTemplateItem: TDSSelectItemInterface[] = [];
    listOfContainerItem: TDSSelectItemInterface[] = [];
    private scrolledIndex = 0;
    @Input() @InputBoolean() open = false;
    @Input() @InputBoolean() autofocus = false;
    showSearch = true;
    @Input() @InputBoolean() allowClear = false;
    @Input() @InputBoolean() allowSearch = true;
    @Input('aria-describedby') userAriaDescribedBy!: string;
    @Input() id = `tds-select-${TDSSelect.nextId++}`;
    @Input() @InputBoolean() backdrop = false;
    @Input() @InputBoolean() dropdownMatchSelectWidth = true;
    @Input() dropdownClassName: string | null = null;
    @Input() mode: TDSSelectModeType = 'default';
    @Input() compareWith: (o1: any, o2: any) => boolean = (o1: any, o2: any) => o1 === o2;
    @Input() @InputBoolean() serverSearch = false;
    @Input() filterOption: TDSFilterOptionType = TDSDefaultFilterOption;
    optionHeightPx = 37;
    @Input() optionOverflowSize = 8;
    @Input() maxMultipleCount = Infinity;
    @Input() @InputBoolean() autoClearSearchValue = true;
    @Input() maxTagCount = Infinity;
    @Input() tokenSeparators: string[] = [];
    @Input() maxTagPlaceholder: TemplateRef<{ $implicit: any[] }> | null = null;
    //dung cho mode  tags / multiple
    @Input() @InputBoolean() autoClose = false;
    @Input()
    get size(): ButtonSize {
        return this._size;
    }
    set size(value: ButtonSize) {
        if (this._size != value) {
            this._size = value;
            this.stateChanges.next();
            switch (this._size) {
                case "sm":
                    this.optionHeightPx = 29;
                    break;
                case "lg":
                    this.optionHeightPx = 45;
                    break;
                default:
                    this.optionHeightPx = 37;
                    break;
            }
        }

    }
    private _size: ButtonSize = "md";
    @Input()
    get placeholder(): string {
        return this._placeholder;
    }
    set placeholder(value: string) {
        this._placeholder = value;
        this.stateChanges.next();
    }
    private _placeholder!: string;

    @Input()
    get required(): boolean {
        return this._required;
    }
    set required(value: boolean) {
        this._required = coerceBooleanProperty(value);
        this.stateChanges.next();
    }
    private _required = false;

    @Input()
    get disabled(): boolean {
        return this._disabled;
    }
    set disabled(value: boolean) {
        this._disabled = coerceBooleanProperty(value);
        this.stateChanges.next();
    }
    private _disabled = false;

    value: TDSSafeAny | Array<TDSSafeAny> = undefined;
    errorState: boolean = false;
    updateErrorState() {
        const oldState = this.errorState;
        const parent = this._parentFormGroup || this._parentForm;
        // const matcher = this.errorStateMatcher || this._defaultErrorStateMatcher;
        const control = this.ngControl ? this.ngControl.control as FormControl : null;
        const newState = this._isErrorState(control, parent);
        if (newState !== oldState) {
            this.errorState = newState;
            this.stateChanges.next();
            this.cdr.markForCheck();
        }
    }
    private _isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
        return TDSIsErrorState(control, form);
    }
    @Input()
    get textField(): string {
        return this._textField;
    }
    set textField(val: string) {
        if (val !== this._textField) {
            this._textField = val;
            this.stateChanges.next();
        }
    }
    private _textField = 'lable';
    @Input()
    get valueField(): string {
        return this._valueField;
    }
    set valueField(val: string) {
        if (val !== this._valueField) {
            this._valueField = val;
            this.stateChanges.next();
        }
    }
    private _valueField = 'value';
    @Input()
    get disabledField(): string {
        return this._disabledField;
    }
    set disabledField(val: string) {
        if (val !== this._disabledField) {
            this._disabledField = val;
        }
    }
    private _disabledField = 'disabled';

    @Input()
    get groupField(): string {
        return this._groupField;
    }
    set groupField(val: string) {
        if (val !== this._groupField) {
            this._groupField = val;
        }
    }
    private _groupField = 'group';
    @Input()
    get data(): Array<TDSSafeAny> {
        return this._data;
    }
    set data(val: Array<TDSSafeAny> | TDSSafeAny) {
        if (TDSHelperArray.hasListValue(val)) {
            this._data = val;
        } else {
            this._data = [];
        }
        this.stateChanges.next();
    }
    private _data: Array<TDSSafeAny> = [];
    @Input() @InputBoolean() loading: boolean = false;
    @Input() @InputBoolean() hideSelected = false;
    @Input() tdsCssOption: NgClassType | null = null;
    isShowPlaceholder = true;
    isShowSingleLabel = false;
    isComposing = false;


    @Input() border: boolean = true;
    @Input() valuePrimitive: boolean = true;
    @Input() classIcon: string | null = "tdsi-arrow-down-line";
    parentIsFormField: boolean = false;
    @Output() readonly onSearch = new EventEmitter<string>();
    @Output() readonly scrollToBottom = new EventEmitter<void>();
    @Output() readonly openChange = new EventEmitter<boolean>();
    @Output() readonly blurChange = new EventEmitter<void>();
    @Output() readonly focusChange = new EventEmitter<void>();
    @Output('selectChange') readonly selectChangeEvent = new EventEmitter<TDSSafeAny>();
    @ViewChild(CdkVirtualScrollViewport, { static: false }) cdkVirtualScrollViewport!: CdkVirtualScrollViewport;


    @ViewChild(CdkConnectedOverlay, { static: true }) cdkConnectedOverlay!: CdkConnectedOverlay;
    @ViewChild('selectOverlay', { static: true }) _connectionSelectOverlay!: ElementRef;
    @ViewChild(TDSSelectSearchComponent, { static: false }) tdsSelectSearch!: TDSSelectSearchComponent;
    @ContentChildren(TDSOption, { descendants: true }) listOfOptionComponent!: QueryList<TDSOption>;
    @ContentChildren(TDSOptgroup, { descendants: true }) listOfOptionGroupComponent!: QueryList<TDSOptgroup>;




    // custom templates
    @ContentChild(TDSOptionTemplateDirective, { read: TemplateRef }) optionTemplate!: TemplateRef<any>;
    @ContentChild(TDSOptionTemplateDirective, { read: TemplateRef }) optgroupTemplate!: TemplateRef<any>;
    @ContentChild(TDSLabelTemplateDirective, { read: TemplateRef }) labelTemplate!: TemplateRef<any>;
    @ContentChild(TDSMultiLabelTemplateDirective, { read: TemplateRef }) multiLabelTemplate!: TemplateRef<any>;
    @ContentChild(TDSHeaderTemplateDirective, { read: TemplateRef }) headerTemplate!: TemplateRef<any>;
    @ContentChild(TDSFooterTemplateDirective, { read: TemplateRef }) footerTemplate!: TemplateRef<any>;
    @ContentChild(TDSNotFoundTemplateDirective, { read: TemplateRef }) notFoundTemplate!: TemplateRef<any>;
    @ContentChild(TDSTypeToSearchTemplateDirective, { read: TemplateRef }) typeToSearchTemplate!: TemplateRef<any>;
    @ContentChild(TDSLoadingTextTemplateDirective, { read: TemplateRef }) loadingTextTemplate!: TemplateRef<any>;
    @ContentChild(TDSTagTemplateDirective, { read: TemplateRef }) tagTemplate!: TemplateRef<any>;
    @ContentChild(TDSLoadingSpinnerTemplateDirective, { read: TemplateRef }) loadingSpinnerTemplate!: TemplateRef<any>;
    timerRightIcon: TDSSafeAny;
    timerScrollActive:TDSSafeAny;
    paddingRightIcon = 0;
    @ViewChild('rigthContainer', { static: true }) _rigthContainer!: ElementRef;
    constructor(
        private _focusMonitor: FocusMonitor,
        private _elementRef: ElementRef,
        private host: ElementRef<HTMLElement>,
        @Optional() @Inject(TDS_FORM_FIELD) @Host() public _formField: TDSFormField,
        @Optional() @Self() public ngControl: NgControl,
        private cdr: ChangeDetectorRef,
        private platform: Platform,
        @Optional() public _parentForm: NgForm,
        @Optional() public _parentFormGroup: FormGroupDirective,
        private ngZone: NgZone
    ) {
        if (this.ngControl != null) {
            this.ngControl.valueAccessor = this;

        }
        if (this._formField) {
            // this.size = this._formField.size;
            this.parentIsFormField = true;
        }
    }
    ngOnInit(): void {
        let that = this;

        this._focusMonitor
            .monitor(this._elementRef, true)
            .pipe(takeUntil(this._destroy))
            .subscribe(focusOrigin => {

                if (!focusOrigin) {
                    this.focused = false;
                    this.cdr.markForCheck();
                    this.blurChange.emit();
                    Promise.resolve().then(() => {
                        this.onTouched();
                    });
                } else {
                    this.focused = true;
                    this.cdr.markForCheck();
                    this.focusChange.emit();
                }
            });
        combineLatest([this.listOfValue$, this.listOfTemplateItem$])
            .pipe(takeUntil(this._destroy))
            .subscribe(([listOfSelectedValue, listOfTemplateItem]) => {
                const listOfSelected = !this.valuePrimitive && TDSHelperObject.hasValue(this.value) ? this.covertModelToList(this.value, this.mode) : [];
                const listConvertDataOfSelected = listOfSelected.map(item => {
                    return this.generateDataItem(item);
                })
                const listOfTagItem = listOfSelectedValue
                    .filter(() => this.mode === 'tags')
                    .filter(value => listOfTemplateItem.findIndex(o => this.compareWith(o.valueField, value)) === -1 && listConvertDataOfSelected.findIndex(o => this.compareWith(o.valueField, value)) === -1)
                    .map(value => this.listOfTopItem.find(o => this.compareWith(o.valueField, value)) || this.generateTagItem(value));
                this.listOfTagAndTemplateItem = [...listOfTemplateItem, ...listOfTagItem];
                this.listOfTopItem = this.listOfValue
                    .map(v => {
                        const value = TDSHelperObject.hasValue(v) && TDSHelperObject.hasValue(v![that.valueField]) ? v![that.valueField] : v;
                        return [...this.listOfTagAndTemplateItem, ...this.listOfTopItem, ...listConvertDataOfSelected].find(item => {
                            return this.compareWith(value, item.valueField)
                        })!
                    }
                    )
                    .filter(item => !!item);

                this.updateListOfContainerItem();
                this.updateTemplateVariable();
                this.updatePaddingRightIcon()

            });

        this.ngZone.runOutsideAngular(() =>
            fromEvent(this.host.nativeElement, 'click')
                .pipe(takeUntil(this._destroy))
                .subscribe(() => {
                    if ((this.open && this.showSearch) || this.disabled) {
                        return;
                    }
                    this.ngZone.run(() => {                        
                        if (!this.focused && !this.disabled) {
                            this.focused = true;
                            this.stateChanges.next();                            
                        }
                        this.setOpenState(!this.open);
                    });
                })
        );

    }
    ngOnChanges(changes: SimpleChanges): void {
        const { open, disabled, data, textField, valueField, disabledField, valuePrimitive, loading } = changes;

        if (open) {
            this.onOpenChange();
        }
        if (disabled && this.disabled) {
            this.setOpenState(false);
            this.setFocused(false);
        }

        if (TDSHelperObject.hasValue(this.data)) {

            this.isReactiveDriven = true;
            const listOfOptions = this.data || [];
            const listOfTransformedItem = listOfOptions.map(item => {
                return this.generateDataItem(item);
            });
            this.listOfTemplateItem$.next(listOfTransformedItem);
        }

    }
    ngAfterContentInit(): void {

    }
    updateListOfValue(listOfValue: Array<any>): void {
        const covertListToModel = (list: Array<any>, mode: TDSSelectModeType): Array<any> | any => {
            if (mode === 'default') {
                if (list.length > 0) {
                    return list[0];
                } else {
                    return null;
                }
            } else {
                return list;
            }
        };
        const model = covertListToModel(listOfValue, this.mode);
        if (this.value !== model) {
            this.listOfValue = listOfValue;
            this.listOfValue$.next(listOfValue);
            this.value = model;
            if (!this.valuePrimitive) {
                if (this.mode == 'default') {
                    const data = TDSHelperArray.hasListValue(this.listOfTopItem) ? this.listOfTopItem[0].data : null
                    this.onChange(data);
                    this.selectChangeEvent.emit(data);
                } else {
                    const data = this.listOfTopItem.map(res => {
                        return res.data;
                    })
                    this.onChange(data);
                    this.selectChangeEvent.emit(data);
                }

            } else {
                this.onChange(this.value);
                this.selectChangeEvent.emit(this.value);
            }

        }
    }
    onClearSelection(e: MouseEvent): void {
        e.preventDefault();
        e.stopPropagation();
        this.onTouched();
        this.updateListOfValue([]);
    }
    updateListOfContainerItem(): void {

        let listOfContainerItem = this.listOfTagAndTemplateItem
            .filter(item => !item.hide)
            .filter(item => {
                if (!this.serverSearch && this.inputValue) {
                    return this.filterOption(this.inputValue, item);
                } else {
                    return true;
                }
            });
        if (this.mode === 'tags' && this.inputValue) {
            const matchedItem = this.listOfTagAndTemplateItem.find(item => item.textField === this.inputValue);
            if (!matchedItem) {
                const tagItem = this.generateTagItem(this.inputValue);
                listOfContainerItem = [tagItem, ...listOfContainerItem];
                this.activatedValue = tagItem.valueField;
            } else {
                this.activatedValue = matchedItem.valueField;
            }
        }
        const optionNotDisabled = this.listOfContainerItem.filter(item => item.type === 'item').find(item => !item.disabled);
        const activatedItem =
            listOfContainerItem.find(item => item.textField === this.inputValue) ||
            listOfContainerItem.find(item => this.compareWith(item.valueField, this.listOfValue[0]))
            || optionNotDisabled;
        this.activatedValue = (activatedItem && activatedItem.valueField) || null;
        let listOfGroupLabel: Array<string | number | TemplateRef<any> | null> = [];
        if (this.isReactiveDriven) {
            listOfGroupLabel = [...new Set(this.data.filter(o => o[this.groupField]).map(o => o[this.groupField]!))];
        } else {
            if (this.listOfOptionGroupComponent) {
                listOfGroupLabel = this.listOfOptionGroupComponent.map(o => o.label);
            }
        }
        /** insert group item **/
        listOfGroupLabel.forEach(label => {
            const index = listOfContainerItem.findIndex(item => label === item.groupLabel);
            if (index > -1) {
                const groupItem = { groupLabel: label, type: 'group', key: label } as TDSSelectItemInterface;
                listOfContainerItem.splice(index, 0, groupItem);
            }
        });
        this.listOfContainerItem = [...listOfContainerItem];
        this.updateCdkConnectedOverlayPositions();
    }
    generateTagItem(value: string): TDSSelectItemInterface {
        return {
            valueField: value,
            textField: value,
            type: 'item',
            item: value,
            data: value
        };
    }
    generateDataItem(item: TDSSafeAny): TDSSelectItemInterface {
        let isOject = TDSHelperObject.hasValue(item[this.valueField]) || TDSHelperObject.hasValue(item[this.textField])
        return {
            textField: typeof item[this.textField] === 'string' || typeof item[this.textField] === 'number' ? item[this.textField] : item,
            valueField: isOject ? item[this.valueField] : item,
            disabled: item[this.disabledField] || false,
            hide: item.hide || false,
            groupLabel: item[this.groupField] || null,
            type: 'item',
            key: item[this.valueField] || item,
            data: item,
            item: item,
        };
    }
    onChange = (_: any) => { };
    onTouched = () => { };
    writeValue(modelValue: any | Array<any>): void {
        /** https://github.com/angular/angular/issues/14988 **/
        if (this.value !== modelValue) {
            this.value = modelValue;
            const listOfValue = this.covertModelToList(modelValue, this.mode).map(f => {
                return f![this.valueField] ? f![this.valueField] : f
            });
            this.listOfValue = listOfValue;
            this.listOfValue$.next(listOfValue);
            this.cdr.markForCheck();
        }
    }
    covertModelToList(model: Array<any> | any, mode: TDSSelectModeType): Array<any> {
        if (model === null || model === undefined) {
            return [];
        } else if (mode === 'default') {
            if (TDSHelperObject.hasValue(model)) {
                if (TDSHelperArray.isArray(model)) {
                    if (TDSHelperString.hasValueString(model[0]))
                        return [model[0]];
                    else
                        return [];
                } else
                    return [model];
            } else {
                return [];
            }

        } else {
            return model;
        }
    };
    registerOnChange(fn: any): void {
        this.onChange = fn;
    }

    registerOnTouched(fn: any): void {
        this.onTouched = fn;
    }
    setDisabledState(isDisabled: boolean) {
        this.disabled = isDisabled;
    }
    ngAfterViewInit() {
        if (this.autofocus && !this.focused) {
            this.setFocused(true);
            this.setOpenState(true);
        }

    }
    ngOnDestroy() {
        this._focusMonitor.stopMonitoring(this._elementRef);
        this._destroy.next();
        this._destroy.complete();
        this.stateChanges.complete();
        if (this.timerRightIcon) {
            clearTimeout(this.timerRightIcon)
        }
      
    }
    setDescribedByIds(ids: string[]) {
        if (ids.length) {
            this._elementRef.nativeElement.setAttribute('aria-describedby', ids.join(' '));
        } else {
            this._elementRef.nativeElement.removeAttribute('aria-describedby');
        }
    }
    onContainerClick(e: MouseEvent) {
        // e.preventDefault();
        // e.stopPropagation();
    }
    updateTemplateVariable(): void {
        const isSelectedValueEmpty = this.listOfTopItem.length === 0;
        this.isShowPlaceholder = isSelectedValueEmpty && !this.isComposing && !this.inputValue;
        this.isShowSingleLabel = !isSelectedValueEmpty && !this.isComposing && !this.inputValue;
    }
    isComposingChange(isComposing: boolean): void {
        this.isComposing = isComposing;
        this.updateTemplateVariable();
    }
    onInputValueChange(value: string): void {

        if (value !== this.inputValue) {
            this.inputValue = value;
            this.updateTemplateVariable();
            this.updateListOfContainerItem();
            this.onSearch.emit(value);
            this.updateCdkConnectedOverlayPositions();
            this.tokenSeparate(value, this.tokenSeparators);
            this.scrollActive();
        }

    }

    onItemClick(value: any): void {
        this.activatedValue = value;
        if (this.mode === 'default') {
            if (this.listOfValue.length === 0 || !this.compareWith(this.listOfValue[0], value)) {
                this.updateListOfValue([value]);
            }
            this.setFocused(false);
            this.setOpenState(false);

        } else {

            const targetIndex = this.listOfValue.findIndex(o => this.compareWith(o, value));
            if (targetIndex !== -1) {
                const listOfValueAfterRemoved = this.listOfValue.filter((_, i) => i !== targetIndex);
                this.updateListOfValue(listOfValueAfterRemoved);
            } else if (this.listOfValue.length < this.maxMultipleCount) {
                const listOfValueAfterAdded = [...this.listOfValue, value];
                this.updateListOfValue(listOfValueAfterAdded);
            }

            if (this.autoClose) {
                this.setFocused(false);
                this.setOpenState(false);
            } else {
                this.focus();
                if (this.autoClearSearchValue) {
                    this.clearInput();
                }
            }
        }
    }
    onItemHover(value: TDSSafeAny): void {
        // TODO: keydown.enter won't activate this value
        this.activatedValue = value;
    }
    onItemDelete(e: MouseEvent, item: TDSSelectItemInterface): void {
        e.preventDefault();
        e.stopPropagation();
        if (!this.disabled) {
            const listOfSelectedValue = this.listOfValue.filter(v => !this.compareWith(v, item.valueField));
            this.updateListOfValue(listOfSelectedValue);
            this.clearInput();
        }

    }

    focus(): void {
        if (this.tdsSelectSearch) {
            this.tdsSelectSearch.focus();
        }
    }

    blur(): void {
        if (this.tdsSelectSearch) {
            this.tdsSelectSearch.blur();
        }
    }

    onOverlayKeyDown(e: KeyboardEvent): void {
        if (e.keyCode === ESCAPE) {
            this.setFocused(false);
            this.setOpenState(false);
            this.onTouched();
        }
    }
    onClickOutside(event: MouseEvent): void {

        if (!this._elementRef.nativeElement.contains(event.target)) {
            this.setFocused(false);
            this.setOpenState(false);
            this.onTouched();
            this.clearInput();
        }
    }
    setOpenState(value: boolean): void {

        if (this.open !== value) {
            this.open = value;
            this.openChange.emit(value);
            this.onOpenChange();
            this.scrollActive();          
            this.cdr.markForCheck();
        }

    }
    setFocused(value: boolean) {
        this.focused = value;
        this.stateChanges.next();
    }
    onOpenChange(): void {
        this.updateCdkConnectedOverlayStatus();
        this.clearInput();
        if (this.open && this.activatedValue) {
            this.scrollToActivatedValue();
        }
    }
    clearInput(): void {
        if (this.tdsSelectSearch)
            this.tdsSelectSearch.clearInputValue();
    }
    onPositionChange(position: ConnectedOverlayPositionChange): void {
        this.dropDownPosition = position.connectionPair.originY;
    }
    showClear() {

        return this.allowClear && !this.disabled && this.listOfValue.length
    }
    get overlayOrigin() {
        if (this._formField) {
            return new CdkOverlayOrigin(this._formField.getConnectedOverlayOrigin());
        } else {
            return new CdkOverlayOrigin(this._connectionSelectOverlay);
        }
    }
    get originElement() {
        if (this._formField) {
            return this._formField.getConnectedOverlayOrigin();
        } else {
            return this._connectionSelectOverlay;
        }
    }
    get showPlaceholder() {
        return TDSHelperString.hasValueString(this.placeholder)
            && this.listOfTopItem.length == 0
            // && (!TDSHelperString.hasValueString(this.inputValue) && this.mode == "multiple")
            // && (this.mode == "default" || (!this.focused && !this.open && this.mode == "multiple"))
            && (!this.focused && !this.open)
    }
    get isSearchDefault() {
        return (this.focused || this.open) && this.allowSearch
    }
    updateCdkConnectedOverlayStatus(): void {
        if (this.platform.isBrowser && this.originElement.nativeElement) {
            reqAnimFrame(() => {
                this.triggerWidth = this.originElement.nativeElement.getBoundingClientRect().width;
                this.cdr.markForCheck();
            });
        }
    }

    updateCdkConnectedOverlayPositions(): void {
        reqAnimFrame(() => {
            this.cdkConnectedOverlay?.overlayRef?.updatePosition();
        });
    }
    trackValue(_index: number, option: TDSSelectItemInterface): any {
        return option.key;
    }


    onScrolledIndexChange(index: number): void {
        this.scrolledIndex = index;
        if (index === this.listOfContainerItem.length - this.optionOverflowSize) {
            this.scrollToBottom.emit();
        }
    }

    scrollToActivatedValue(): void {
        if(!TDSHelperObject.hasValue(this.cdkVirtualScrollViewport) || !this.open || !TDSHelperObject.hasValue(this.activatedValue))
        {
            return;
        }
        const index = this.listOfContainerItem.findIndex(item => this.compareWith(item.key, this.activatedValue));
        if (index < this.scrolledIndex || index >= this.scrolledIndex + this.optionOverflowSize) {
            this.cdkVirtualScrollViewport.scrollToIndex(index || 0);
        }

    }
    ngDoCheck() {

        if (this.ngControl) {
            this.updateErrorState();
        }
    }
    tokenSeparate(inputValue: string, tokenSeparators: string[]): void {
        const includesSeparators = (str: string | string[], separators: string[]): boolean => {
            // tslint:disable-next-line:prefer-for-of
            for (let i = 0; i < separators.length; ++i) {
                if (str.lastIndexOf(separators[i]) > 0) {
                    return true;
                }
            }
            return false;
        };
        const splitBySeparators = (str: string | string[], separators: string[]): string[] => {
            const reg = new RegExp(`[${separators.join()}]`);
            const array = (str as string).split(reg).filter(token => token);
            return [...new Set(array)];
        };
        if (
            inputValue &&
            inputValue.length &&
            tokenSeparators.length &&
            this.mode !== 'default' &&
            includesSeparators(inputValue, tokenSeparators)
        ) {
            const listOfLabel = splitBySeparators(inputValue, tokenSeparators);
            this.onTokenSeparate(listOfLabel);
        }
    }
    onTokenSeparate(listOfLabel: string[]): void {
        const listOfMatchedValue = this.listOfTagAndTemplateItem
            .filter(item => listOfLabel.findIndex(label => label === item.textField) !== -1)
            .map(item => item.valueField)
            .filter(item => this.listOfValue.findIndex(v => this.compareWith(v, item)) === -1);
        if (this.mode === 'multiple') {
            this.updateListOfValue([...this.listOfValue, ...listOfMatchedValue]);
        } else if (this.mode === 'tags') {
            const listOfUnMatchedLabel = listOfLabel.filter(
                label => this.listOfTagAndTemplateItem.findIndex(item => item.textField === label) === -1
            );
            this.updateListOfValue([...this.listOfValue, ...listOfMatchedValue, ...listOfUnMatchedLabel]);
        }
        this.clearInput();
    }
    onKeyDown(e: KeyboardEvent): void {
        if (this.disabled || this.mode === 'tags') {
            return;
        }

        const listOfFilteredOptionNotDisabled = this.listOfContainerItem
            .filter(item => item.type === 'item')
            //thêm trường hợp hideSelected =true
            .filter(item => !item.disabled && (!this.hideSelected || this.listOfValue.find(key => key === item.key) == null));
        const activatedIndex = listOfFilteredOptionNotDisabled.findIndex(item => this.compareWith(item.valueField, this.activatedValue));
        switch (e.keyCode) {
            case UP_ARROW:
                e.preventDefault();
                if (this.open) {
                    const preIndex = activatedIndex > 0 ? activatedIndex - 1 : listOfFilteredOptionNotDisabled.length - 1;
                    this.activatedValue = listOfFilteredOptionNotDisabled[preIndex].valueField;
                }
                break;
            case DOWN_ARROW:
                e.preventDefault();
                if (this.open) {
                    const nextIndex = activatedIndex < listOfFilteredOptionNotDisabled.length - 1 ? activatedIndex + 1 : 0;
                    this.activatedValue = listOfFilteredOptionNotDisabled[nextIndex].valueField;
                } else {
                    this.setOpenState(true);
                }
                break;
            case ENTER:
                e.preventDefault();
                if (this.open) {
                    if (TDSHelperObject.hasValue(this.activatedValue)) {
                        this.onItemClick(this.activatedValue);
                    }
                } else {
                    this.setOpenState(true);
                }
                break;
            case SPACE:
                if (!this.open) {
                    this.setOpenState(true);
                    e.preventDefault();
                }
                break;
            case TAB:
                this.setOpenState(false);
                break;
            case ESCAPE:
                /**
                 * Skip the ESCAPE processing, it will be handled in {@link onOverlayKeyDown}.
                 */
                break;
            default:
                if (!this.open) {
                    this.setOpenState(true);
                }
        }
        this.scrollToActivatedValue();
    }
    updatePaddingRightIcon() {
        if (this.timerRightIcon) {
            clearTimeout(this.timerRightIcon)
        }
        if (TDSHelperObject.hasValue(this.classIcon)) {
            const padding = 8;
            let widthIcon = this.size == 'md' ? 28 : this.size == 'lg' ? 32 : 24;

            if (this.showClear()) {
                widthIcon = this.size == 'md' ? 56 : this.size == 'lg' ? 64 : 48;
            }
          
            this.timerRightIcon = setTimeout(() => {
                this.paddingRightIcon = widthIcon + padding;
                this.cdr.markForCheck();
            }, 0);
        } else {
            this.timerRightIcon = setTimeout(() => {
                this.paddingRightIcon = 0;
                this.cdr.markForCheck();
            }, 0);
        }

    
    }
    private scrollActive()
    {
        if (this.timerScrollActive) {
            clearTimeout(this.timerScrollActive)
        }
        this.timerScrollActive = setTimeout(() => {
            this.scrollToActivatedValue();
        }, 0);
        
    }
    static ngAcceptInputType_open: BooleanInput;
    static ngAcceptInputType_allowClear: BooleanInput;
    static ngAcceptInputType_showSearch: BooleanInput;
    static ngAcceptInputType_loading: BooleanInput;
    static ngAcceptInputType_autoFocus: BooleanInput;
    static ngAcceptInputType_autoClearSearchValue: BooleanInput;
}