import { BACKSPACE } from '@angular/cdk/keycodes';
import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    Host,
    Input,
    OnChanges,
    OnInit,
    Optional,
    Output,
    SimpleChanges,
    TemplateRef,
    ViewChild,
    ViewEncapsulation
} from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { TDSSelectSearchComponent } from './select-search';
import { TDSSelectItemInterface, TDSSelectModeType, TDSSelectTopControlItemType } from './select.type';

@Component({
    selector: 'tds-select-value-control',
    exportAs: 'tdsSelectValueControl',
    preserveWhitespaces: false,
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None,
    template: `
  
  `,
    host: {
        '(keydown)': 'onHostKeydown($event)'
    }
})
export class TDSSelectValueControlComponent implements OnChanges {
    @Input() id: string | null = null;
    @Input() showSearch = false;
    @Input() placeHolder: string | TemplateRef<any> | null = null;
    @Input() open = false;
    @Input() maxTagCount: number = Infinity;
    @Input() autofocus = false;
    @Input() disabled = false;
    @Input() mode: TDSSelectModeType = 'default';
    @Input() customTemplate: TemplateRef<{ $implicit: TDSSelectItemInterface }> | null = null;
    @Input() maxTagPlaceholder: TemplateRef<{ $implicit: any[] }> | null = null;
    @Input() removeIcon: TemplateRef<any> | null = null;
    @Input() listOfTopItem: TDSSelectItemInterface[] = [];
    @Input() tokenSeparators: string[] = [];
    @Output() readonly tokenize = new EventEmitter<string[]>();
    @Output() readonly inputValueChange = new EventEmitter<string>();
    @Output() readonly deleteItem = new EventEmitter<TDSSelectItemInterface>();
    @ViewChild(TDSSelectSearchComponent) tdsSelectSearchComponent!: TDSSelectSearchComponent;
    listOfSlicedItem: TDSSelectTopControlItemType[] = [];
    isShowPlaceholder = true;
    isShowSingleLabel = false;
    isComposing = false;
    inputValue: string | null = null;
    onHostKeydown(e: KeyboardEvent): void {
        const inputValue = (e.target as HTMLInputElement).value;
        if (e.keyCode === BACKSPACE && this.mode !== 'default' && !inputValue && this.listOfTopItem.length > 0) {
            e.preventDefault();
            this.onDeleteItem(this.listOfTopItem[this.listOfTopItem.length - 1]);
        }
    }

    updateTemplateVariable(): void {
        const isSelectedValueEmpty = this.listOfTopItem.length === 0;
        this.isShowPlaceholder = isSelectedValueEmpty && !this.isComposing && !this.inputValue;
        this.isShowSingleLabel = !isSelectedValueEmpty && !this.isComposing && !this.inputValue;
    }

    isComposingChange(isComposing: boolean): void {
        this.isComposing = isComposing;
        this.updateTemplateVariable();
    }

    onInputValueChange(value: string): void {
       
        if (value !== this.inputValue) {
            this.inputValue = value;
            this.updateTemplateVariable();
            this.inputValueChange.emit(value);
            this.tokenSeparate(value, this.tokenSeparators);
        }
    }

    tokenSeparate(inputValue: string, tokenSeparators: string[]): void {
        const includesSeparators = (str: string | string[], separators: string[]): boolean => {
            // tslint:disable-next-line:prefer-for-of
            for (let i = 0; i < separators.length; ++i) {
                if (str.lastIndexOf(separators[i]) > 0) {
                    return true;
                }
            }
            return false;
        };
        const splitBySeparators = (str: string | string[], separators: string[]): string[] => {
            const reg = new RegExp(`[${separators.join()}]`);
            const array = (str as string).split(reg).filter(token => token);
            return [...new Set(array)];
        };
        if (
            inputValue &&
            inputValue.length &&
            tokenSeparators.length &&
            this.mode !== 'default' &&
            includesSeparators(inputValue, tokenSeparators)
        ) {
            const listOfLabel = splitBySeparators(inputValue, tokenSeparators);
            this.tokenize.next(listOfLabel);
        }
    }

    clearInputValue(): void {
        if (this.tdsSelectSearchComponent) {
            this.tdsSelectSearchComponent.clearInputValue();
        }
    }

    focus(): void {
        if (this.tdsSelectSearchComponent) {
            this.tdsSelectSearchComponent.focus();
        }
    }

    blur(): void {
        if (this.tdsSelectSearchComponent) {
            this.tdsSelectSearchComponent.blur();
        }
    }

    trackValue(_index: number, option: TDSSelectTopControlItemType): any {
        return option.valueField;
    }

    onDeleteItem(item: TDSSelectItemInterface): void {
        if (!this.disabled && !item.disabled) {
            this.deleteItem.next(item);
        }
    }

    constructor(private elementRef: ElementRef) {
        // TODO: move to host after View Engine deprecation
        this.elementRef.nativeElement.classList.add('tds-select-selector');
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { listOfTopItem, maxTagCount, customTemplate, maxTagPlaceholder } = changes;
        if (listOfTopItem) {
            this.updateTemplateVariable();
        }
        if (listOfTopItem || maxTagCount || customTemplate || maxTagPlaceholder) {
            const listOfSlicedItem: TDSSelectTopControlItemType[] = this.listOfTopItem.slice(0, this.maxTagCount).map(o => {
                return {
                    textField: o.textField,
                    valueField: o.valueField,
                    disabled: o.disabled,
                    contentTemplateOutlet: this.customTemplate,
                    contentTemplateOutletContext: o
                };
            });
            if (this.listOfTopItem.length > this.maxTagCount) {
                const exceededLabel = `+ ${this.listOfTopItem.length - this.maxTagCount} ...`;
                const listOfSelectedValue = this.listOfTopItem.map(item => item.valueField);
                const exceededItem = {
                    textField: exceededLabel,
                    valueField: '$$__tds_exceeded_item',
                    disabled: true,
                    contentTemplateOutlet: this.maxTagPlaceholder,
                    contentTemplateOutletContext: listOfSelectedValue.slice(this.maxTagCount)
                };
                listOfSlicedItem.push(exceededItem);
            }
            this.listOfSlicedItem = listOfSlicedItem;
        }
    }
  
}