import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TDSSelect } from './select';
import { TDSSelectSearchComponent } from './select-search';
import { BidiModule } from '@angular/cdk/bidi';
import { OverlayModule } from '@angular/cdk/overlay';
import { PlatformModule } from '@angular/cdk/platform';
import { FormsModule } from '@angular/forms';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { A11yModule } from '@angular/cdk/a11y';
import {
  TDSFooterTemplateDirective,
  TDSHeaderTemplateDirective,
  TDSItemLabelDirective,
  TDSLabelTemplateDirective,
  TDSLoadingSpinnerTemplateDirective,
  TDSLoadingTextTemplateDirective,
  TDSMultiLabelTemplateDirective,
  TDSNotFoundTemplateDirective,
  TDSOptgroupTemplateDirective,
  TDSOptionTemplateDirective,
  TDSTagTemplateDirective,
  TDSTypeToSearchTemplateDirective
} from './select.directive';
import { TDSSelectOptionGroup } from './select-option-group';
import { TDSOptionItem } from './select-option-item';
import { TDSOutletModule } from 'tds-ui/core/outlet';
import { TDSSelectPlaceholderComponent } from './select-placeholder';
import { TDSSelectValueControlComponent } from './select-value';
import { TDSEmptyModule } from 'tds-ui/empty';
import { TDSSpinnerModule } from 'tds-ui/progress-spinner';


const tmpDirective = [
  TDSOptgroupTemplateDirective,
  TDSOptionTemplateDirective,
  TDSLabelTemplateDirective,
  TDSMultiLabelTemplateDirective,
  TDSHeaderTemplateDirective,
  TDSFooterTemplateDirective,
  TDSNotFoundTemplateDirective,
  TDSTypeToSearchTemplateDirective,
  TDSLoadingTextTemplateDirective,
  TDSTagTemplateDirective,
  TDSLoadingSpinnerTemplateDirective,
  TDSItemLabelDirective
]
const Cpm =[
  TDSSelectOptionGroup,
  TDSOptionItem,
  TDSSelectPlaceholderComponent,
  TDSSelectValueControlComponent
]
@NgModule({
  declarations: [
    TDSSelect,
    TDSSelectSearchComponent,
    tmpDirective,
    Cpm
  ],
  imports: [
    CommonModule,
    FormsModule,
    PlatformModule,
    OverlayModule,
    BidiModule,
    CommonModule,
    ScrollingModule,
    A11yModule,
    TDSOutletModule,
    TDSEmptyModule,
    TDSSpinnerModule
  ],
  exports: [
    TDSSelect,
    TDSSelectSearchComponent,
    tmpDirective,
    Cpm
  ]
})
export class TDSSelectModule { }
