

import { FocusMonitor } from '@angular/cdk/a11y';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    Output,
    Renderer2,
    SimpleChanges,
    ViewChild,
    ViewEncapsulation
} from '@angular/core';
import { COMPOSITION_BUFFER_MODE } from '@angular/forms';
import { BehaviorSubject, Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, takeUntil } from 'rxjs/operators';

@Component({
    selector: 'tds-select-search',
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    template: `
     <input
       #inputElement
       [attr.id]="id"
       autocomplete="off"
       class="text-body-2 font-regular dark:text-d-neutral-1-900 text-neutral-1-40 focus:outline-none focus:border-transparent shadow-none border-0 bg-transparent text-current pl-0 pb-0 pt-0 m-0 w-full outline-none  box-border placeholder-neutral-1-400"
       [ngModel]="value"
       [attr.autofocus]="autofocus ? 'autofocus' : null"
       [disabled]="disabled"
       [style.opacity]="showInput ? null : 0"
       (ngModelChange)="onValueChange($event)"
       (compositionstart)="setCompositionState(true)"
       (compositionend)="setCompositionState(false)"
       style="box-shadow: unset;"
     />
   `,
    host: {
        class: "box-border flex items-center h-full w-full"
    },
    providers: [{ provide: COMPOSITION_BUFFER_MODE, useValue: false }]
})
export class TDSSelectSearchComponent implements AfterViewInit, OnChanges, OnInit, OnDestroy {
    @Input() id: string | null = null;
    @Input() disabled = false;
    @Input() showInput = true;
    @Input() focusTrigger = false;
    @Input() value = '';
    @Input() autofocus = false;
    @Output() readonly valueChange = new EventEmitter<string>();
    @Output() readonly isComposingChange = new EventEmitter<boolean>();
    @ViewChild('inputElement', { static: true }) inputElement!: ElementRef;
    // private searchChange$ = new BehaviorSubject('');
    private _destroy = new Subject<void>();
    setCompositionState(isComposing: boolean): void {
        this.isComposingChange.next(isComposing);
    }
    onValueChange(value: string): void {
        this.value = value;
        this.valueChange.emit(value);
    }
    clearInputValue(): void {
        const inputDOM = this.inputElement.nativeElement;
        inputDOM.value = '';
        this.onValueChange('');
    }
    focus(): void {
        this.focusMonitor.focusVia(this.inputElement, 'keyboard');
    }
    blur(): void {
        this.inputElement.nativeElement.blur();
    }
    constructor(private elementRef: ElementRef, private renderer: Renderer2, private focusMonitor: FocusMonitor) {
        // TODO: move to host after View Engine deprecation
        this.elementRef.nativeElement.classList.add('tds-select-selection-search');
    }
    ngOnChanges(changes: SimpleChanges): void {
        const inputDOM = this.inputElement.nativeElement;
        const { focusTrigger, showInput } = changes;
        if (showInput) {
            if (this.showInput) {
                this.renderer.removeAttribute(inputDOM, 'readonly');
            } else {
                this.renderer.setAttribute(inputDOM, 'readonly', 'readonly');
            }
        }        
        // IE11 cannot input value if focused before removing readonly
        if (focusTrigger && focusTrigger.currentValue === true && !focusTrigger.previousValue) {
            inputDOM.focus();         
        }
    }
    ngOnInit() {
        // this.searchChange$
        //     .pipe(
        //         debounceTime(500),
        //         distinctUntilChanged(),
        //         takeUntil(this._destroy),
        //     )
        //     .subscribe(f => {

        //         this.valueChange.next(f);
        //     })
    }
    ngAfterViewInit(): void {

        if (this.autofocus) {
            this.focus();
        }
    }
    ngOnDestroy() {
        this._destroy.next();
        this._destroy.complete();
    }
}
